import './style.css'
import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader'
import {GLTFLoader} from 'three/examples/jsm/loaders/GLTFLoader'
import Guify from 'guify'

import tunnelFragmentShader from './shaders/tunnel/fragment.glsl'
import tunnelVertexShader from './shaders/tunnel/vertex.glsl'


import headFragmentShader from './shaders/head/fragment.glsl'
import headVertexShader from './shaders/head/vertex.glsl'

/**
 * Debug
 */

 const gui = new Guify({
    align: 'right',
    theme: 'dark',
    barMode: 'none'
})

const guiDummy = {
    backgroundColor: '#42093b'
};


gui
    .Register({

        object: guiDummy,
        property: 'backgroundColor',
        type: 'color',
        format: 'hex',
        label: 'linesCount',
        min: 1,
        max: 10,
        step: 1,
        onChange: ()=>{
            scene.background = new THREE.Color(guiDummy.backgroundColor)
        }
    })

/**
 * Draco
 */

const gltfLoader  = new GLTFLoader()
const dracoLoader = new DRACOLoader();
dracoLoader.setDecoderPath('./draco/')
gltfLoader.setDRACOLoader(dracoLoader)

/**
 * Texture
 */

const loader = new THREE.TextureLoader()

const texture = loader.load('./lines.jpg')
const textureSmoke = loader.load('./smoke2.png')

texture.wrapS = THREE.RepeatWrapping;
texture.wrapT = THREE.RepeatWrapping;
texture.magFilter = THREE.NearestFilter;

textureSmoke.wrapS = THREE.RepeatWrapping;
textureSmoke.wrapT = THREE.RepeatWrapping;
textureSmoke.magFilter = THREE.NearestFilter;

console.log(texture);
/**
 * Base
 */

// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new THREE.Scene()
//scene.fog = new THREE.FogExp2( '#42093b', 1 );

/**
 * Sizes
 */
 const sizes = {
    width: window.innerWidth,
    height: window.innerHeight,
    windowHalfX: window.innerWidth / 2,
    windowHalfY: window.innerHeight / 2
}

/**
 * Mouse
 */

let mouseX = 0, mouseY = 0

let target = new THREE.Vector3()

window.addEventListener('mousemove', (event) => {

    mouseX = ( event.clientX - sizes.windowHalfX );
    mouseY = ( event.clientY - sizes.windowHalfY );

})




window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 100)
camera.position.x = 0
camera.position.y = 0
camera.position.z = 3
//camera.lookAt(0,0,2)
scene.add(camera)

// Controls
const controls = new OrbitControls(camera, canvas)
controls.enableDamping = true



/**
 * Model
 * 
 */

const headMaterial = new THREE.ShaderMaterial({
    vertexShader: headVertexShader,
    fragmentShader: headFragmentShader,
    uniforms: {
        time: {value: 0},
        uTexture: {value: texture},
        progress1: {value: 0},
    },
    
})


let model;
gltfLoader.load(
    './model.glb',
    (gltf) => {
        model = gltf.scene.children[0]
        model.scale.set(0.5,0.5,0.5)
        model.position.z = 0
        model.position.x = 0
        model.position.y = 0
        //model.rotateY(Math.PI)

        model.material = headMaterial;
        //scene.add(model)
    }
)

const boxGeo = new THREE.BoxBufferGeometry(1,1,1,100,100,100)
const box = new THREE.Mesh(boxGeo, headMaterial)

scene.add(box)

/**
 * Tunnel
 */
const material = new THREE.ShaderMaterial({
    vertexShader: tunnelVertexShader,
    fragmentShader: tunnelFragmentShader,
    uniforms: {
        time: {value : 0},
        progress1: {value: 0},
        smokeTexture: { value: textureSmoke}
    },
    side:THREE.DoubleSide,
    transparent: true,
    depthWrite:false,
    depthTest:false,
    blending :THREE.AdditiveBlending
})
const tunnelGeometry = new THREE.PlaneBufferGeometry(0.3, 0.3,1,1)
const instanceGeometry = new THREE.InstancedBufferGeometry()

instanceGeometry.attributes = tunnelGeometry.attributes
instanceGeometry.index = tunnelGeometry.index

let number = 2000
let translateArray = new Float32Array(number*3)
let rotationArray = new Float32Array(number)
for (let i = 0; i < number; i++) {
    let theta = Math.random() * 2 * Math.PI
    translateArray.set([
        Math.sin(theta) * (1 + i /number) ,
        Math.cos(theta) * (1 + i / number * 2) ,
        Math.random() * 5
    ],3*i)
    rotationArray.set([
        Math.random()*2*Math.PI,
    ], i);
    
}

instanceGeometry.setAttribute('translate', new THREE.InstancedBufferAttribute(translateArray,3))
instanceGeometry.setAttribute('aRotate', new THREE.InstancedBufferAttribute(rotationArray,1))
const plane = new THREE.Mesh(instanceGeometry, material)

scene.add(plane)





/**
 * Light 
 */
// scene.add(new THREE.DirectionalLight('#ffffff', 10))
/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    antialias: true,
})
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
renderer.outputEncoding = THREE.sRGBEncoding
/**
 * Animate
 */
const clock = new THREE.Clock()
let lastElapsedTime = 0

const tick = () =>
{
    const elapsedTime = clock.getElapsedTime()
    const deltaTime = elapsedTime - lastElapsedTime
    lastElapsedTime = elapsedTime
    //console.log(Math.abs(target.x  + (-( mouseX - target.x ) * .00035)));
    if( Math.abs(target.x  + (-( mouseX - target.x ) * .00035)) <= 20 ){
        target.x += -( mouseX - target.x ) * .00035;
    }
    if( Math.abs(target.y  + (( mouseX - target.y ) * .00035)) <= 20){
        target.y += (  mouseY - target.y ) * .00035;
    }
    
    target.z = camera.position.z - 13; // assuming the camera is located at ( 0, 0, z );

    if(box) {
        box.rotateY(Math.PI)
        box.lookAt(target)
        console.log(target);
    }
    

    // Update controls
    controls.update()

    material.uniforms.time.value = elapsedTime;
    headMaterial.uniforms.time.value = elapsedTime;
    //sconsole.log(headMaterial.uniforms.time.value);
    // Render
    renderer.render(scene, camera)
    //console.log(target);

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()